* {
  box-sizing: border-box;
}

:root {
  --breakpoint-small: 23.4375em;
  --white-color: #fff;
  --light-gray-color: #d6e2f0;
  --grayish-blue-color: #7b879d;
  --dark-blue-color: #1f3251;
  --card-width: 20rem;
  --card-height: 31.0625rem;
  --heading-size: 1.375rem;
  --heading-line: 1.75rem;
  --paragraph-size: .9375rem;
  --paragraph-line: 1.1875rem;
  --paragraph-letter: .0117188rem;
  --content-size: 18rem;
}

body {
  background-color: var(--light-gray-color);
  font-family: Outfit, sans-serif;
}

h1 {
  color: var(--dark-blue-color);
  margin: 0;
  font-weight: 700;
}

p {
  color: var(--grayish-blue-color);
  font-weight: 400;
}

.container {
  width: 100vw;
  height: 100vh;
  min-width: var(--breakpoint-small);
  justify-content: center;
  align-items: center;
  display: flex;
}

.card {
  background-color: var(--white-color);
  width: var(--card-width);
  height: var(--card-height);
  border-radius: 1.5rem;
  flex-direction: column;
  align-items: center;
  display: flex;
  box-shadow: 0 .75rem 3.125rem #0000001a;
}

.class__qrcode {
  padding: 1rem 0 1.2rem;
}

.card__image {
  width: var(--content-size);
  height: var(--content-size);
  border-radius: 1rem;
}

.card__content {
  width: var(--content-size);
}

.card__heading {
  font-size: var(--heading-size);
  line-height: var(--heading-line);
  text-align: center;
}

.card__paragraph {
  font-size: var(--paragraph-size);
  line-height: var(--paragraph-line);
  letter-spacing: var(--paragraph-letter);
  text-align: center;
  padding: 0 1rem;
}

.attribution {
  color: var(--grayish-blue-color);
  text-align: center;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.attribution a {
  color: var(--dark-blue-color);
  text-decoration: none;
}

.attribution a:hover {
  text-decoration: underline;
}

